import { useState, useEffect } from 'react'

const ImageSearch = ({ searchText, term }) => {
  const [text, setText] = useState('');
  const onSubmit = (e) => {
    e.preventDefault()
    searchText(text)
  }

  useEffect(() => {
    setText(term)
  }, [term])
  return (
    <div className="max-w-sm rounded overflow-hidden my-10 mx-auto">
      <form onSubmit={onSubmit} className="w-full max-w-sm">
        <div className="flex items-center border-b-2 border-teal-500 py-2">
          <input onChange={e => {
            setText(e.target.value)
            e.target.value = text
          }} className="appearance-none bg-transparent border-none w-full text-gray-700 mr-3 py-1 px-2 leading-tight focus:outline-none" type="text" placeholder={"Search Image Term..."} value={text} />
          <button className="flex-shrink-0 bg-green-500 hover:bg-green-700 border-green-500 hover:border-green-700 text-sm border-4 text-white py-1 px-2 rounded duration-300" type="submit">Search</button>
        </div>
      </form>
    </div>
  )
}

export default ImageSearch
